import { bgGreen, bgRed, bgYellow, green, red, yellow } from 'colors';

export namespace Output {
  export function info(message: string) {
    console.log('Info: ' + message);
  }

  export function error(message: string, exitProcess = false) {
    console.log(`${bgRed(' Error: ')} ${red(message)}\n`);
    if (exitProcess) {
      process.exit();
    }
  }

  export function success(message: string) {
    console.log(`${bgGreen(' Success: ')} ${green(message)}\n`);
  }

  export function warning(message: string) {
    console.log(`${bgYellow(' Warning: ')} ${yellow(message)}\n`);
  }

  export function clear() {
    console.clear();
  }
}
